<template>
  <main class="h-full w-full overflow-auto">
    <section class="relative box-border min-h-screen w-full overflow-auto pb-[84px] md:rounded-2xl">
      <nav class="relative mt-[41px] flex w-full items-end justify-between p-[14px]">
        <img src="@/static/images/Group.svg" alt="" />
        <C-Select
          class="w-[130px] text-center"
          :list="merchentList"
          v-model="selected"
          :placeholder="'選擇店家'"
          item-text="name"
          item-value="id"
        ></C-Select>
      </nav>
      <div
        v-if="MerchantNewsTicker"
        class="m-auto my-6 mx-[14px] box-border h-fit w-[calc(100%-28px)] rounded-[10px] border border-black py-[7px] px-4 text-left"
      >
        <div class="flex-start flex items-center">
          <font-awesome-icon icon="fa-regular fa-volume" class="mr-4 text-2xl" />
          <div class="newsTickerText text-left text-lg">
            <span>{{ MerchantNewsTicker }}</span>
          </div>
        </div>
      </div>
      <div
        class="m-auto my-6 mx-[14px] box-border flex h-fit w-[calc(100%-28px)] flex-wrap items-center justify-between rounded-[10px] bg-white py-[7px] px-4"
      >
        <div class="flex items-center justify-center">
          <div class="relative">
            <div class="h-20 w-20 overflow-hidden rounded-full bg-slate-300 object-cover">
              <font-awesome-icon
                v-if="userAvatarImgUrl === ''"
                icon="fa-regular fa-user"
                class="mt-[calc(50%-1.25rem)] text-4xl"
              />
              <img v-else :src="userAvatarImgUrl" :alt="userInfo ? userInfo.user.name : '大頭照'" />
            </div>
            <div
              class="absolute -right-2 bottom-0 flex h-7 w-7 items-center justify-center rounded-full bg-[#5784D5]"
            >
              <font-awesome-icon
                icon="fa-light fa-pen"
                class="text-xl text-white"
                @click="toggleDialog"
              />
            </div>
          </div>
          <div class="text-left text-lg leading-8 ml-8">
            <p>{{ userInfo ? userInfo.user.name : "" }}</p>
            <p>{{ GETTER_MECHANTNAME }}</p>
          </div>
          <a  v-if="!isBindingLine" href="https://liff.line.me/1657658841-5ZXBn22E"  class="text-left text-3xl leading-8 ml-4 text-slate-400  cursor-pointer ">
            <font-awesome-icon icon="fa-brands fa-line" class="block text-center m-auto mb-1"/><span class="text-sm block">綁定line帳號</span>
          </a>
          <p v-else   class="text-left text-3xl leading-8 ml-4  text-green-600">
            <font-awesome-icon icon="fa-brands fa-line" /><span class="hidden">.</span>
          </p>
        </div>
        <font-awesome-icon
          icon="fa-regular fa-right-from-bracket"
          class="cursor-pointer text-2xl"
          @click="doLogout"
        />
      </div>

      <div class="m-auto box-border flex h-fit w-full flex-wrap justify-around gap-4 px-[14px]">
        <template v-for="(service, index) in servicesList">
          <div
            v-if="isAuthority(service.itemName)"
            @click="doRoute(service.routeName)"
            @keydown="doRoute(service.routeName)"
            class="flex h-[100px] w-[165px] cursor-pointer flex-col items-center justify-center gap-3 rounded-[10px] border border-solid border-[#AEAEAE] bg-white shadow-2xl"
            :key="service.routeName + index"
          >
            <img :src="service.iconSrc" alt="" />
            <!-- 這邊名流後端需要調整 2022/09/13 -->
            <div>{{ service.itemName === "設計師日報表" ? "報表" : service.itemName }}</div>
          </div>
        </template>
      </div>
      <div
        class="fixed bottom-5 right-4 flex h-14 w-14 cursor-pointer items-center justify-center rounded-[28px] border border-solid bg-white shadow-xl"
        @click="shareLink"
        @keypress="shareLink"
      >
        <font-awesome-icon icon="fa-regular fa-share-nodes" class="mr-1 text-4xl text-[#365cad]" />
      </div>
    </section>
    <C-Modal-Dialog
      v-model="showAvatarDialog"
      :animationCss="'animate__animated animate__fade animate__fast'"
      class="max-w-[370px]"
    >
      <form @submit.prevent="handleSubmitAvatar">
        <div>
          <div v-if="filechange" class="max-h-[50vh]">
            <img :src="imageBlob" ref="image" alt="" />
          </div>
        </div>
        <label for="uploadimg">
          <div class="rounded-md border border-dashed">
            <font-awesome-icon icon="fa-regular fa-upload" class="mr-2" @click="doRoute" />
            <span>上傳大頭照</span>
          </div>
          <input type="file" id="uploadimg" @change="uploadFile" class="hidden" />
        </label>
        <C-Checkbox-Full v-model="isGlobal" label="上傳至所有店家"></C-Checkbox-Full>
        <C-Button-Wrap>
          <button
            @click="toggleDialog"
            class="w-full rounded-xl border border-solid border-primary bg-white px-6 py-3 text-sm font-bold leading-tight text-primary shadow-md transition duration-150 ease-in-out hover:shadow-lg focus:shadow-lg focus:outline-none focus:ring-0 active:shadow-lg"
          >
            關閉
          </button>
          <button
            :disabled="imageBlob === '' || imageBlob === null"
            type="submit"
            :class="[{ 'bg-primary/30': imageBlob === '' || imageBlob === null }]"
            class="w-full rounded-xl bg-primary px-6 py-3 text-sm font-bold leading-tight text-white shadow-md transition duration-150 ease-in-out hover:shadow-lg focus:shadow-lg focus:outline-none focus:ring-0 active:shadow-lg"
          >
            確認上傳
          </button>
        </C-Button-Wrap>
      </form>
    </C-Modal-Dialog>
  </main>
</template>

<script>
import { mapActions, mapState, mapGetters } from 'vuex';
import { isJSON } from '@/utils/index';
import Cropper from 'cropperjs';
import 'cropperjs/dist/cropper.css';

export default {
  name: 'home-index',
  metaInfo: {
    title: 'BeautyOS 設計師',
  },
  data() {
    return {
      myCropper: null,
      afterImg: '',
      afterImgFile: null,
      selected: '',
      imageBlob: '',
      showAvatarDialog: false,
      filechange: false,
      userAvatarImgUrl: '',
      isGlobal: false,
      merchentList: [],
      MerchantNewsTicker: '',
      isBindingLine: false,
      servicesList: [
        {
          itemName: '預約審查',
          routeName: 'Order',
          iconSrc: require('../../static/images/calendar-check-regular.svg'),
        },
        {
          itemName: '打卡',
          routeName: 'Punch',
          iconSrc: require('../../static/images/clock-regular.svg'),
        },
        {
          itemName: '行事曆',
          routeName: 'ScheduleBreak',
          iconSrc: require('../../static/images/calendar-days-regular.svg'),
        },
        {
          itemName: '價目表設定',
          routeName: 'PriceList',
          iconSrc: require('../../static/images/file-invoice-dollar-regular.svg'),
        },
        // { itemName: '發優惠券', routeName: 'Coupon', iconSrc: require('../../static/images/ticket-regular.svg') },
        // { itemName: '發紅利點數', routeName: 'Bonus', iconSrc: require('../../static/images/point.svg') },
        {
          itemName: '設計師日報表',
          routeName: 'Report',
          iconSrc: require('../../static/images/chart-mixed-solid.svg'),
        },
        {
          itemName: '客戶資料查詢',
          routeName: 'Customer',
          iconSrc: require('../../static/images/users-light.svg'),
        },
      ],
    };
  },
  computed: {
    ...mapState('userInfo', ['userInfo', 'currentMechantId', 'userAuthorizeList']),
    ...mapGetters('userInfo', ['GETTER_MECHANTNAME']),
    isAuthority() {
      return (name) => this.userAuthorizeList.find((item) => item.pageName === name && item.authority);
    },
  },
  mounted() {
    this.init();
  },
  methods: {
    ...mapActions('userInfo', [
      'SET_USER_PROFILE',
      'SET_USER_CURRENT_MERCHANTID',
      'SET_USER_AUTHORIZELIST',
    ]),
    async init() {
      await this.getMechants();
      await this.bindingLine();

      const [firstUserMerchant] = this.userInfo.user.merchants;
      this.selected = this.currentMechantId || firstUserMerchant.id || '';
    },

    async bindingLine() {
      // userBindWithLine
      let data = '';
      let errors = '';
      const urlParams = new URLSearchParams(window.location.search);
      const myParam = urlParams.get('code');
      const submit = {
        code: myParam,
      };
      // console.log(myParam !== null && !this.isBindingLine);

      if (myParam !== null && !this.isBindingLine) {
        const resp = await this.$api.userBindWithLine(submit);
        data = resp.data;
        errors = resp.errors;
        if (errors) return;
        if (data.data.userBindWithLine) {
          await this.getMe();
          this.$swal.fire({
            icon: 'success',
            html: '<p>Line 帳號綁定成功<br>畫面將重新載入</p>',
            showConfirmButton: false,
            showCancelButton: false,
            showClass: {
              popup: 'animate__animated animate__fadeInDown',
            },
            hideClass: {
              popup: 'animate__animated animate__fadeOutUp animate__faster',
            },
            customClass: {
              container: '!z-[99999]',
            },
            timer: 3000,
            timerProgressBar: true,
          }).then(() => {
            window.location.href = new URL('/designer', window.location.href);
          });
        }
      }
    },
    async uploadFile(e) {
      this.filechange = false;
      const reader = new FileReader();

      reader.addEventListener(
        'load',
        () => {
          // convert image file to base64 string
          this.imageBlob = reader.result;
          this.filechange = true;
          this.$nextTick().then(() => {
            this.initCropper();
          });
        },
        false,
      );

      if (e.target.files[0]) {
        reader.readAsDataURL(e.target.files[0]);
      }
    },
    toggleDialog() {
      this.showAvatarDialog = !this.showAvatarDialog;
      if (this.imageBlob !== '' && this.imageBlob !== null && this.showAvatarDialog) {
        this.$nextTick().then(() => {
          this.initCropper();
        });
      } else {
        this.imageBlob = '';
      }
    },
    initCropper() {
      this.myCropper = new Cropper(this.$refs.image, {
        viewMode: 1,
        dragMode: 'none',
        initialAspectRatio: 1,
        aspectRatio: 1,
        preview: '.before',
        background: false,
        autoCropArea: 0.6,
        zoomOnWheel: false,
        toggleDragModeOnDblclick: true,
      });
    },
    async handleSubmitAvatar() {
      this.afterImg = await this.myCropper
        .getCroppedCanvas({
          maxWidth: 300,
          maxHeight: 300,
          fillColor: '#fff',
          imageSmoothingQuality: 'high',
        })
        .toDataURL('image/jpeg');
      const res = await fetch(this.afterImg);
      const blob = await res.blob();
      this.afterImgFile = new File([blob], 'avatar.jpeg', { type: 'image/jpeg' });

      const resp = await this.$api.setUserAvatar(this.afterImgFile, this.isGlobal);
      const { errors } = resp.data;
      if (errors) return;
      this.$swal
        .fire({
          title: ' ',
          html: `<div class="flex items-center justify-center my-10">
          <img src="/designer/assets/images/success.svg" alt="" class="inline-block mr-1" /><span>上傳成功</span>
        </div>`,
          showConfirmButton: false,
          showCloseButton: true,
          timer: 3000,
          timerProgressBar: true,
        })
        .then(async () => {
          this.afterImg = '';
          this.afterImgFile = null;
          await this.getMechants();
          this.toggleDialog();
        });
    },

    async shareLink() {
      const url = `${window.location.protocol}//${window.location.hostname}/login/?merchantId=${this.currentMechantId}&designerId=${this.userInfo.user.id}`;

      if (navigator.share) {
        await navigator
          .share({
            title: '點選下方連結 預約我唷',
            url,
          })
          .then(() => console.log('success'))
          .catch((err) => {
            window.open(url);
            console.error(err);
          });
      } else {
        window.open(url);
      }
    },

    doRoute(routName) {
      this.$router.push({ name: routName });
    },

    async getMechants() {
      const res = await this.$api.user({ id: this.userInfo.user.id });
      const { data, errors } = res.data;
      if (errors) return;
      this.merchentList = data.user.merchants;
      this.userAvatarImgUrl = data.user.avatar;
      this.isBindingLine = !!data.user.lineAccountId;
    },
    doLogout() {
      this.$swal
        .fire({
          icon: 'question',
          title: '您確定要登出嗎?',
          showConfirmButton: true,
          showCancelButton: true,
          showClass: {
            popup: 'animate__animated animate__fadeInDown',
          },
          hideClass: {
            popup: 'animate__animated animate__fadeOutUp animate__faster',
          },
        })
        .then(async (res) => {
          if (res.isConfirmed) {
            const resp = await this.$api.userLogout();
            const { data, errors } = resp.data;
            if (errors) return;
            if (!data.userLogout) return;
            await this.SET_USER_PROFILE(null);
            this.$swal
              .fire({
                icon: 'success',
                title: '登出成功',
                showConfirmButton: false,
                showCancelButton: false,
                showClass: {
                  popup: 'animate__animated animate__fadeInDown',
                },
                hideClass: {
                  popup: 'animate__animated animate__fadeOutUp animate__faster',
                },
                timer: 3000,
                timerProgressBar: true,
              })
              .then(() => {
                this.$router.replace({ name: 'Login' });
              });
          }
        });
    },
    async getMe() {
      const resp = await this.$api.me();
      const { data, errors } = resp.data;
      if (errors) return;
      try {
        this.merchantIdentity = data.me.merchantIdentity;
        this.isBindingLine = !!data.me.lineAccountId;
      } catch {
        this.merchantIdentity = {};
      }
    },
    async getMerchantAuthorities() {
      const resp = await this.$api.getMerchantAuthorities();
      const { data, errors } = resp.data;
      if (errors) return;
      try {
        this.merchantAuthorities = data.getMerchantAuthorities;
      } catch {
        this.merchantAuthorities = [];
      }
    },
    async getMerchantNewsTicker() {
      const resp = await this.$api.getMerchantNewsTicker();
      const { data, errors } = resp.data;
      if (errors) return;
      try {
        this.MerchantNewsTicker = data.getMerchantNewsTicker;
      } catch {
        this.MerchantNewsTicker = '';
      }
    },

    permissionReportList() {
      return new Promise((resolve) => {
        const identityAuthorities = this.merchantAuthorities.find(
          (item) => item.id === this.merchantIdentity.authorityId,
        );
        const authorityList = identityAuthorities && isJSON(identityAuthorities.authoritySettings)
          ? JSON.parse(identityAuthorities.authoritySettings)
          : [];
        const reportList = authorityList.flatMap((authority) => (authority.pageTitle !== '設計師端權限設定' ? [] : authority.pageAuthority));
        const permissionReportList = reportList.filter((item) => item.authority);
        resolve(permissionReportList);
      });
    },
    async swtichAuthority(value) {
      await this.SET_USER_CURRENT_MERCHANTID(value);
      await this.getMe();
      await this.getMerchantAuthorities();
      await this.getMerchantNewsTicker();
      const permissionReportList = await this.permissionReportList();
      await this.SET_USER_AUTHORIZELIST(permissionReportList);
      await this.getMechants();
    },
  },
  watch: {
    selected(value) {
      this.swtichAuthority(value);
    },
  },
};
</script>

<style scoped lang="scss">
.container {
  display: flex;
}
.before {
  width: 100px;
  height: 100px;
  overflow: hidden;
}
::v-deep .cropper-view-box,
::v-deep .cropper-face {
  border-radius: 50%;
}
/* The css styles for `outline` do not follow `border-radius` on iOS/Safari (#979). */
.cropper-view-box {
  outline: 0;
  box-shadow: 0 0 0 1px #39f;
}

.newsTickerText {
  width: 100%;
  position: relative;
  align-self: flex-start;
  height: 28px;
  overflow: hidden;
  white-space: nowrap;
}

.newsTickerText span {
  position: absolute;
  padding-left: 100%;
  animation: marquee 15s linear infinite;
}

@keyframes marquee {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-100%);
  }
}
</style>
